.resume-container {
  width: 600px;
  text-align: center;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.my-resume-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* height: 250px; */
  gap: 30px;
  /* width: 100%; */
}
#email-input {
  width: 300px;
  color: black;
  height: 50px;
  font-size: 20px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.my-resume {
  width: 100%;
}

.download-button-container {
  width: 80%;
}

@media screen and (max-width: 760px) {
  .resume-container {
    width: 100%;
  }
  .my-resume {
    min-width: 100%;
  }
  .my-resume-container {
    /* height: 300px; */
  }
  /* #email-input {
    width: 100%;
  } */
}

@media screen and (max-width: 480px) {
  .resume-container {
    width: 100%;
  }
  .my-resume {
    min-width: 100%;
  }
  .my-resume-container {
    /* height: 200px; */
  }
  #email-input {
    width: 100%;
  }
}
