.project-item-card {
  display: flex;
  flex-direction: column;
  justify-content: top;
  background-color: rgb(27, 25, 25);

  /* height: 380px; */
  width: 550px;
  margin-bottom: 35px;
  cursor: pointer;
  border-radius: 20px;
}

.project-item-card:hover {
  position: relative;
  top: 0.3em;
}

.video-player {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex: 1 1 0px;
  transition: transform 500ms;
  background-color: black;
  width: 550px;

  /* width: 500px; */
}

.video-player:hover {
  z-index: 1000;
  transform: scale(1.1);
  background-color: black;
  /* transform: translateY(-5px); */
  box-shadow: 0px 0px 30px 30px rgba(255, 255, 255, 0.25);
}

.card-contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 8px;
  padding-left: 10px;
  padding-right: 10px;
}

.card-contents:hover {
  /* border-radius: 20px; */
  /* border: 2px solid white; */
  /* background-color: black; */
}

.card-contents > * {
  color: white;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.card-contents > h4 {
  animation: changeText 8s linear infinite;
}
.card-contents > h4:hover {
  font-size: large;
  cursor: pointer;
  color: #39ff14 !important;
}

@media screen and (max-width: 760px) {
  .project-item-card {
    /* width: 100%; */
  }
  .video-player {
    /* width: 100%; */
  }
}

@media screen  and (max-width: 510px) {
  .project-item-card {
    width: 100%;
  }
  .video-player {
    width: 100%;
  }
}
